body {
    margin: 0;
    padding: 0;
  }
  
  /* Background settings for the animated section of the website  */
  .sky {
    overflow-x: hidden;
    position: relative;
    background-image: linear-gradient( #80bfd8, #f3b55f, #35722c, #35adf3);
    width: 100%;
    height: 250vh;
    display: flex;
    justify-content: center;
    scroll-behavior: smooth;
  }
  
  /* Default settings for images in animated section */
  .sky img {
    position: absolute;
    bottom: 0;
    z-index: 2;
  }
  
  /* Container for "About Me" section */
  .info {
    box-sizing: border-box;
    width: 75vh;
    max-width: 95%;
    margin: 162vh auto 52vh;
    border: solid black 2px;
    border-radius: 30px;
    background-color:whitesmoke;
    box-shadow: 0px 6px 12px rgb(0, 0, 0, 0.7);
    display:inline-table;
    z-index: 2;
  }
  
  /* Headings in "About Me" section */
  .subtitles {
    text-align: center;
    font-size: 3.5vh;
    font-weight:900;
    margin: 2vh auto 1vh;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.75);
    font-family: 'BioRhyme';
  }
  
  /* Text in "About Me" section */
  .text {
    font-size: 2.5vh;
    margin: 0vh 3vh 0vh;
    min-width: 45%;
    text-align: center;
  }

  /* Head shot image in "About me" section */
  .hs {
    width: 40vh;
    border-radius: 30px;
    margin-bottom: 19vh;
    border: solid black 2px;
    box-shadow: 0px 6px 12px rgb(0, 0, 0, 0.7);
    z-index: 3;
  }
  
  /****************** Individual settings for each image in animated section *******************/
