  /* ----- CSS Animations --------- */
  /* Scroll Down Arrow */
  .section {
    position: relative;
    cursor: pointer;
    z-index: 2;
    left: 47%;
  }
  .section::before {
    animation: bounce 1.5s ease infinite;
    bottom: 164vh;
    color: #fff;
    content: '╲╱';
    font-size: 3vh;
    letter-spacing: -1px;
    line-height: 4rem;
    filter:drop-shadow(0px 5px 6px black);
    position: absolute;
    text-align: center;
    border-radius: 10px;
    padding: 0rem .5rem;
    background-color: rgb(0, 0, 0, 0.1);
  }
  @keyframes bounce {
    50% {
      transform: translateY(-80%);
    }
  }
  /* Scroll Down Arrow 2 */
  .section2 {
    position: relative;
    cursor: pointer;
    z-index: 4;
    left: 17%;
  }
  .section2::before {
    animation: slide 1.5s ease infinite;
    bottom: 144vh;
    color: #fff;
    content: '╲╱';
    font-size: 3vh;
    letter-spacing: -1px;
    line-height: 4rem;
    filter:drop-shadow(0px 5px 6px black);
    position: absolute;
    text-align: center;
    border-radius: 10px;
    padding: 0rem .5rem;
    background-color: rgb(0, 0, 0, 0.1);
  }
  @keyframes slide {
    50% {
      transform: translateX(-80%);
    }
  }

  /* Fade on load */
  .mountains, .sun, .pg, .rocket, .html, .css, .javascript, .typescript, .node, .postgresql, .python, .git, .mui, .jQ, .redux, .react, .saga, .sql  {
    animation: fade 5s;
  }
  @keyframes fade {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

  /* Fade + Scale on load */
  .username {
    animation: scaleUp 5s;
  }
  @keyframes scaleUp {
    0% {opacity:0; transform: scale(.2);}
    100% {opacity:1; transform: scale(1);}
  }
  
